import axiosInstance from '../middleware/Interceptor'


 

function getAccessList () {
  return axiosInstance.get('/siem-core/user/getAccessList')

}

function getAllUsers(){
    return axiosInstance.get("/siem-core/user/getAllUsersWithinCompany?company=Technominds")
}






const userService = {
    getAccessList,
    getAllUsers
}

export default userService